html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Helvetica, Arial, sans-serif;
}

body {
  width: 90%;
  margin: auto;
}

h2,
h3 {
  color: #444444;
}

p {
  color: #666;
}

input {
  font-size: 1rem;
  width: 100%;
}

li {
  list-style: none;
}

.card {
  width: 250px;
  height: 200px;
  border-radius: 8px;
  margin: 0.5rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
  overflow: hidden;
  padding-bottom: 1rem;
}

img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.title-image {
  margin: 1rem;
  text-overflow: ellipsis;
  word-wrap: break-word;
  line-height: 1rem;
  max-height: 2em;
  overflow: hidden;
}
